<!-- 正在热映板块 -->
<template>
  <section id="liveShow">
    <!-- 上方banner -->
    <div class="bannerSwiper">
      <mt-swipe :auto="2000" ref="swiperWrapper">
        <mt-swipe-item>
          <img
            src="http://img.zcool.cn/community/01d90956ce816832f875520fded72e.jpg@900w_1l_2o"
            alt
          />
        </mt-swipe-item>
        <mt-swipe-item>
          <img
            src="https://img.zcool.cn/community/01bda658fb9286a8012160f7ea2df0.jpg@1280w_1l_2o_100sh.jpg"
            alt
          />
        </mt-swipe-item>
        <mt-swipe-item>
          <img
            src="http://img.zcool.cn/community/0135bc58fb9531a8012160f7818b5d.jpg@900w_1l_2o_100sh.jpg"
            alt
          />
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!-- 电影列表 -->
    <ul class="filmList">
      <li class="bot_bor_c" v-for="(item,index) in filmData" :key="index">
        <div class="filmCon">
          <div class="flex">
            <div class="img"><img class="image" :src="item.imgUrl" alt=""></div>
            <div class="intro bot_bor_c ellipsis" @click="sendFilmData(index)">
              <router-link class="filmLink" :to="{name:'filmDetail',params:{hid:index}}">
                <h3 class="ellipsis">{{item.name}}</h3>
                <div class="rank">{{item.rank}}</div>
                <p
                  class="detail mt6 ellipsis"
                >{{item.intro}}</p>
                <p class="mt6 ellipsis">{{item.actor}}</p>
              </router-link>
            </div>
          </div>
          <button class="buy">购买</button>
        </div>
        <div class="filmAd flex">
            <p class="ellipsis">{{item.adLeft}} </p>
            <p class="ellipsis">{{item.adRight}}</p>
        </div>
      </li>
    </ul>
  </section>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  name: "liveShow",
  data() {
    //这里存放数据
    return {
        cityCode:'bj',
        filmData:[],
    };
  },
  methods:{
      getFilm(cityCode){
          this.axios.get("filmList").then(res=>{
              if(res.data.result == "success"){
                  this.filmData = res.data.data[cityCode]
              }
          }).catch(err=>{
              console.log(err)
          })
      },
      getCityInfo(){
        this.$root.Bus.$on("city",data =>{
          this.cityCode = data[1]
        })
        console.log("liveShow--getCity",this.cityCode)
      },
      sendFilmData(index){
        this.$root.Bus.$emit("filmIndex",this.filmData[index])
      }
  },
  mounted(){console.log("*****mounted")},
  // updated(){console.log("*****updated")},
  created(){
    console.log("*****created")
      this.getCityInfo();
      this.getFilm(this.cityCode);
      
  },
  watch:{
    cityCode(data){
      this.getFilm(data)
      console.log("--liveshow",data)
    }
  },
  
};
</script>
<style scoped>
.bannerSwiper {
  width: 100%;
  height: 35vmin;
}

.bannerSwiper img {
  width: 100%;
}
/* 电影列表 */
.filmList {
  padding-left: 10px;
  padding-bottom: 50px;
}
.filmList li{
    margin-top: 15px;
}
.filmList .filmCon{position: relative;}
.filmList li .img {
  width: 65px;
  height: 90px;
  border: 1px solid #ccc;
}
.filmList .intro {
  flex: 1;
  margin-left: 10px;
  padding-bottom: 15px;
}
.filmList .intro .filmLink .detail{
    width:75%;
}
.filmList li .filmCon button {
  position: absolute;
  right:20px;
  bottom:50px;
}
/* 下方广告 */
.filmList .filmAd{
    padding:10px 0;
}
.filmList .filmAd p{
    flex:1;
    padding-left: 10px;
}
.filmList .filmAd p:first-child{
    border-right: 1px solid #d9d9d9;
    color:#f60;
    padding-left: 80px;
}
</style>


