<!-- 热门城市 -->
<template>
<section>
    <div class="pointer" @click="cityShow=true">
        <span>{{cityName}}</span>
        <i>>></i>
    </div>
    <!-- 城市列表 -->
    <div class="cityList" v-show="cityShow">
        <div class="title bot_bor">选择热门<i class="pointer" @click="cityShow=false">close</i></div>
        <v-cityList></v-cityList>
    </div>
</section>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import cityList from '@/components/cityList/cityList'
export default {
//import引入的组件需要注入到对象中才能使用
    name:"hotCity",
    components:{
        'v-cityList':cityList
    },
    data(){
        return{
            cityName:"北京",
            cityShow:false
        }
    },
    methods:{
        getCityData(){
            this.$root.Bus.$on("city",data=>{
                this.cityName = data[0];
                this.cityShow = false;
                console.log(data,"---hotcity")
            })
        }
    },
    created(){
        this.getCityData();
    },
    watch:{
        cityShow(data){
            // this.$root.Bus.$off();
            this.getCityData();
        }
    },
    
}
</script>
<style scoped>
    .cityList{
        position: fixed;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background-color: #fff;
        z-index:100;
    }
    .cityList .title{
        position: relative;
        font-size: 22px;
        font-weight: bold;
    }
    .cityList .title i{
        position: absolute;
        right:20px;
    }
</style>


