<!-- 城市列表 -->
<template>
  <section class="cityDetails">
    <div class="citys rig_bor" ref="cityScroll">
      <ul>
        <li>
          <span id="hot">热门</span>
          <div @click.stop="getCity(item)" v-for="(item,index) in hotCity" :key="index">{{item}}</div>
        </li>
        <li v-for="(item,props) in cityList" :key="props">
          <span :id="props">{{props}}</span>
          <div @click.stop="getCity(item.cityName)" v-for="(item,index) in item" :key="index">{{item.cityName}}</div>
        </li>
      </ul>
    </div>

    <ul class="cityCode">
      <li class="pointer">热门</li>
      <li class="pointer" v-for="(item,props) in cityList" :key="props">{{props}}</li>
    </ul>
  </section>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import BScroll from "better-scroll";
export default {
  name: "cityList",
  data() {
    return {
      cityList: [],
      hotCity:["北京","上海","广州"],
      cityCode:'bj',
    };
  },
  methods:{
    getCity(cityName){
      this.cityCode = this.match(cityName);
      //传值
      var cityArr = [];
      cityArr.push(cityName,this.cityCode);
      this.$root.Bus.$emit("city",cityArr)

      console.log(this.cityCode)
    },
    match(cityName){
      var arr = ['bj','sh','gz'];
      var randomCode = arr[Math.floor(Math.random()*arr.length)];
      switch(cityName){
        case "北京":
          return "bj";
          break;
        case "上海":
          return "sh";
          break;
        case "广州":
          return "gz";
          break;
      }
      return randomCode;
      
    }
  },
  created() {
    //获取城市数据
    this.axios
      .get("cityList")
      .then(res => {
        this.cityList = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
   
  },
  mounted() {
    //定时器
    this.$nextTick(() => {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.cityScroll, {
          click: true
        });
      } else if (!this.$refs.cityScroll) {
        return;
      } else {
        this.scroll.refresh();
      }
    });
  },
  
};
</script>
<style scoped>
.cityDetails {
  display: flex;
  height: 100%;
}
.cityDetails .citys {
  flex: 1;
  text-align: left;
  padding-right: 30px;
  overflow: hidden;
}
.cityDetails .citys >ul{
    padding-bottom: 80px;
}
.cityDetails .citys >ul> li > span {
  display: block;
  background-color: #e9e9e9;
  font-size: 20px;
  font-weight: bold;
  padding-left: 20px;
}
.cityDetails .citys>ul > li > div {
  padding-left: 20px;
  cursor: pointer;
}
.cityDetails .cityCode {
  width: 50px;
  line-height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
</style>


