<!-- 电影详情页 -->
<template>
<section class="filmDetail">
    <div class="basisInfo flex">
        <div class="img"><img class="image" :src="filmDetail.imgUrl" alt=""></div>
        <div class="textIntro">
            <h3 class="name">{{filmDetail.name}}</h3>
            <div class="egName">{{filmDetail.egName}}</div>
            <div class="type">{{filmDetail.type}}</div>
            <div class="time">{{filmDetail.time}}</div>
            <div class="comment">{{filmDetail.comment}}</div>
        </div>
    </div>
    <div class="introCon">{{filmDetail.intro}}</div>
    <div class="back t_center" @click="goBack()">返回上页</div>
</section>
</template>

<script>
export default {
//import引入的组件需要注入到对象中才能使用
    name:"filmDetail",
    data() {
    //这里存放数据
        return {
            filmDetail:{}
        };
    },
    methods:{
        getFilmDetail(){
            this.$root.Bus.$on("filmIndex",data=>{
                this.filmDetail=data;
            })
        },
        goBack(){
            history.go(-1)
        }
    },
    created(){
        this.getFilmDetail();
    },
    beforeDestroy(){
        this.$root.Bus.$off();
    }
}
</script>
<style scoped>
    .basisInfo{
        background-color: #2a86ab;
        padding:20px;
    }
    .basisInfo .img{
        width:25vmin;
        height:30vmin;
        border:1px solid #fff;
        margin-right: 15px;
    }
    /* .basisInfo .textIntro{flex:1;} */
    .basisInfo .textIntro .name{color:#fff;}
    .basisInfo .textIntro div{
        line-height: 6vmin;
        color:#fff;
    }
    .introCon{
        padding:20px;
        text-indent: 2em;
        text-align: justify;
    }
    .back{
        position:fixed;
        bottom:0;
        height:70px;
        line-height: 70px;
        width:100%;
        background-color:crimson;
        color:#fff;
        font-size: 20px;
        letter-spacing: 2px;
        cursor: pointer;
        z-index: 999999;
    }

</style>


